.game-popup {
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100vh;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 8, 64, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: bottom 0.5s;
}

.game-popup__body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  padding-top: 120px;
  width: 920px;
  height: 460px;
