}

.game-popup__close {
  border: none;
  padding: 0;
  background-color: transparent;
  position: absolute;
  right: 50px;
  top: 30px;
  width: 40px;
  height: 40px;
}

.game-popup__close span {
  position: absolute;
  left: 0;
  top: 50%;
  display: block;
  width: 60px;
  height: 2px;
  background-color: #b5b5b5;
  transform: translateY(-50%);
